	module.exports = {"data":{"markdownRemark":{"html":"<p>Registration is now open for the <a href=\"http://www.rtpnet.org/conf/\">RTPnet Conference</a>, North Carolina's only annual statewide conference that focuses specifically on nonprofit technology. The conference mission is to help nonprofits use technology more effectively. It's May 20th.</p>\n<p>Conference subjects this year will focus on: Technology Volunteerism, Technology Infrastructure and Technology Innovation. More than 100 people are expected to attend the conference.</p>\n<p>RTPnet is a volunteer-driven, 501 (c)(3) nonprofit corporation dedicated to helping North Carolina nonprofit organizations leverage Internet tools to promote and support their missions. RTPnet has hosted the conference for five straight years around the theme of \"Bridging North Carolina's Human/Digital Divide.\"</p>\n<p>RTPnet offers annual fee-based memberships to North Carolina nonprofits and community technology centers in the Southeast. Member benefits include Internet services and discounts to RTPnet-sponsored events.</p>\n<blockquote class=\"large\">Who should attend?\n<ul>\n<li>Nonprofit professionals</li>\n<li>Technology providers</li>\n<li>Government officials</li>\n<li>School administrators and teachers</li>\n<li>Anyone interested in technology issues affecting the nonprofit sector</blockquote></li>\n</ul>","frontmatter":{"created_at":"March 30, 2005","path":"/2005/03/30/rtpnet-conference-in-nc-usa","title":"RTPNet Conference in NC, USA","tags":["rtpnet","nptech"]}}},"pathContext":{}}

